.shape-divider-control {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
    button {
        background: #fff;
        margin: 10px 5px;
        border: 1px solid #e9e9e9;
        flex: 0 0 29%;
        &.selected {
            border: 1px solid #bcbcbc;
            box-shadow: 0 0 10px #b9b9b9;
        }
    }
}

.components-toolbar {
    button {
        color: #000;
    }
}